html {
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  margin: 1em 0 0 1em;
}

.row {
  /* border: 1px solid #000; */
  margin: 1em 0 1em 0;
}

.date-btn {
  border: 1px solid #ccc;
  text-align: center;
  padding: 2em;
  margin-bottom: 1em;
}

.date-btn:hover {
  cursor: pointer;
}

.date-btn.selected {
  color: #fff;
  background-color: rgb(54, 54, 54);
}

#legend_1, #legend_2 {
  max-width: 250px;
}

#legend_2 {
  position: absolute;
  right: 1em;
}